<template>
  <el-card>
      <el-button @click="info.isShow=true" type="primary" icon="el-icon-circle-plus-outline">新增</el-button>
      <el-divider></el-divider>
      <el-table
      :data="goodsList"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="id"
        min-width="80">
      </el-table-column>
       <el-table-column
        prop="firstcatename"
        label="一级分类"
        min-width="120">
      </el-table-column>
       <el-table-column
        prop="secondcatename"
        label="二级分类"
        min-width="120">
      </el-table-column>
      <el-table-column
        prop="goodsname"
        label="商品名称"
        min-width="120">
      </el-table-column>
      <el-table-column
        prop="img"
        label="商品图片"
        min-width="120">
       <template v-slot="$props">
          <img :src="$imgBaseUrl+$props.row.img" alt="" style="height:80px">
       </template>

      </el-table-column>
      <el-table-column
        prop="price"
        label="零售价格"
        min-width="120">
          <template v-slot="$props">
            &yen; {{$props.row.price}}
          </template>
      </el-table-column>

      <el-table-column
        prop="market_price"
        label="市场价格"
        min-width="120">
        <template v-slot="$props">
           &yen; {{$props.row.market_price}}
         </template>
      </el-table-column>
      <el-table-column
        prop="isnew"
        label="是否新品"
        min-width="120">

        <template v-slot="$props">
          <el-tooltip :content="$props.row.isnew===1?'是':'否'" placement="top">
           <el-switch
            v-model="$props.row.isnew"
            :active-value="1"
            :inactive-value="0">
          </el-switch>
          </el-tooltip>
        </template>

      </el-table-column>
      <el-table-column
        prop="ishot"
        label="是否热卖"
        min-width="120">
        <template v-slot="$props">
          <el-tooltip :content="$props.row.ishot===1?'是':'否'" placement="top">
           <el-switch
            v-model="$props.row.ishot"
            :active-value="1"
            :inactive-value="0">
          </el-switch>
          </el-tooltip>
        </template>

      </el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        min-width="120">
         <template v-slot="$props">
           <el-tag type="success" v-if="$props.row.status===1">正常</el-tag>
           <el-tag type="danger" v-else>禁用</el-tag>
         </template>
      </el-table-column>
      <el-table-column
        label="操作"
        min-width="120"
        fixed="right"
        >
        <template v-slot="$props">
          <el-button @click="edit($props.row)" type="success" icon="el-icon-edit" circle size="mini"></el-button>
          <el-button @click="del($props.row.id)" type="danger" icon="el-icon-delete" circle size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
    background
    layout="prev, pager, next"
    :total="goodsTotal"
    :page-size="size"
    @current-change="pageChange"
    >
  </el-pagination>
  <!-- 调用组件 -->
  <Dialog ref="dialog" :info="info" @updateList="updateList"/>
  </el-card>
</template>

<script>
import Dialog from './Dialog'
import {mapState,mapActions} from 'vuex'
export default {
  components:{
    Dialog
  },
  data(){
    return {
      // 传递给子组件的数据
      info:{
        isShow:false
      },
      page:1,   // 页码
      size:5    // 每页显示数据条数
    }
  },
  computed:{
    ...mapState({goodsList:state=>state.goods.goodsList,goodsTotal:state=>state.goods.goodsTotal})
  },
  methods:{
    ...mapActions('goods',['getGoodsListAction','getGoodsTotalAction']),
    // 监听分页页码更新
    pageChange(page){
      // 1-更新页码变量page
      this.page=page
      // 2-刷新列表视图
      this.getGoodsListAction({page:this.page,size:this.size})

    },
    // 编辑方法
    edit(obj){
      // 显示弹出层组件
      this.info.isShow=true
      // 设置表单数据
      this.$refs.dialog.setInfo(obj)
    },
    del(id){
      this.$myConfirm(()=>{
        this.$http.post('goodsdelete',{id}).then(res=>{
          if(res.code===200){
            // 刷新视图
            this.getGoodsListAction({page:this.page,size:this.size})
            this.getGoodsTotalAction()
            return this.$success(res.msg)
          }
          this.$error(res.msg)
        })
      })
    },
    // 自定义事件updateList回调函数
    updateList(){
      this.getGoodsListAction({page:this.page,size:this.size})
      this.getGoodsTotalAction()
    }
  },
  created(){
    this.getGoodsListAction({page:this.page,size:this.size})
    this.getGoodsTotalAction()
  }
}
</script>

<style>

</style>


