<template>
  <el-dialog
    @close="resetInfo"
    :title="goodsData.id>0?'编辑':'新增'"
    :visible.sync="info.isShow"
    fullscreen
  >
    <el-form ref="goodsForm" :model="goodsData" :rules="rules" label-width="100px">
      <el-tabs v-model="activeName">
        <el-tab-pane label="基本信息" name="first">
          <el-form-item label="一级分类" prop="first_cateid">
            <el-select v-model="goodsData.first_cateid" @change="firstCateChange">
              <el-option
                v-for="item in cateList"
                :key="item.id"
                :value="item.id"
                :label="item.catename"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="二级分类" prop="second_cateid">
            <el-select v-model="goodsData.second_cateid">
              <el-option
                v-for="item in secondCateList"
                :key="item.id"
                :value="item.id"
                :label="item.catename"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品名称" prop="goodsname">
            <el-input placeholder="商品名称" v-model="goodsData.goodsname"></el-input>
          </el-form-item>
          <el-form-item label="商品图片" prop="img">
            <el-upload
              action="#"
              list-type="picture-card"
              :auto-upload="false"
              :limit="1"
              :on-exceed="limitTips"
              :on-change="imgChange"
              :file-list="imgList"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="商品价格" prop="price">
            <el-input placeholder="商品价格" v-model="goodsData.price"></el-input>
          </el-form-item>
          <el-form-item label="市场价格" prop="market_price">
            <el-input placeholder="市场价格" v-model="goodsData.market_price"></el-input>
          </el-form-item>
          <el-form-item label="商品规格" prop="specsid">
            <el-select v-model="goodsData.specsid" @change="specsidChange">
              <el-option
                v-for="item in specsList"
                :key="item.id"
                :value="item.id"
                :label="item.specsname"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="规格属性值" prop="specsattr">
            <!-- multiple: 支持多选 -->
            <el-select v-model="goodsData.specsattr" multiple>
              <el-option
                v-for="(item,index) in specsAttrs"
                :key="index"
                :value="item"
                :label="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="是否新品" prop="isnew">
            <el-radio-group v-model="goodsData.isnew">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="是否热卖" prop="ishot">
            <el-radio-group v-model="goodsData.ishot">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-radio-group v-model="goodsData.status">
              <el-radio :label="1">正常</el-radio>
              <el-radio :label="2">禁用</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="商品详情" name="second">
          <el-form-item prop="description" label-width="0">
            <VueWangeditor
              style="height:480px"
              ref="editor"
              id="editor"
              width="100%"
              v-model="goodsData.description"
            />
          </el-form-item>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="info.isShow = false">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import VueWangeditor from "vue-wangeditor";
export default {
  computed: {
    ...mapState({
      cateList: state => state.category.cateList,
      specsList: state => state.specs.specsList
    })
  },
  components: {
    VueWangeditor
  },
  // 父组件传递过来的数据
  props: ["info"],
  data() {
    // 商品详情自定义校验方法
    const validDesc = (rule, value, callback) => {
      if (this.$refs.editor.getHtml() === "<p><br></p>") {
        return callback(new Error("请输入商品描述"))
      }
      callback()
    }
    // 商品图片自定义校验方法

    const validImg = (rule, value, callback) => {
      if (this.goodsData.img === "") {
        return callback(new Error("请选择商品图片"))
      }
      callback()
    }
    return {
      // 默认显示的tab标签页面
      activeName: "first",
      // 二级分类数组
      secondCateList: [],
      // 规格属性的可选值
      specsAttrs: [],
      // 商品图片数组
      imgList: [],
      // 和表单进行绑定的数据
      goodsData: {
        // 待编辑的商品id
        id: 0,
        // 一级分类id
        first_cateid: "",
        // 二级分类id
        second_cateid: "",
        // 商品规格id
        specsid: "",
        // 商品规格属性值
        specsattr: [],
        //商品名称
        goodsname: "",
        // 商品图片
        img: "",
        // 商品价格
        price: "",
        //  市场价格
        market_price: "",
        // 是否新品
        isnew: "",
        // 是否热卖
        ishot: "",
        // 状态
        status: "",
        // 商品详情
        description: ""
      },
      // 表单校验规则对象
      rules: {
        goodsname: {
          required: true,
          message: "请输入商品名称",
          trigger: "blur"
        },
        price: { required: true, message: "请输入商品价格", trigger: "blur" },
        market_price: {
          required: true,
          message: "请输入市场价格",
          trigger: "blur"
        },
        first_cateid: {
          required: true,
          message: "请选择一级分类",
          trigger: "blur"
        },
        second_cateid: {
          required: true,
          message: "请选择二级分类",
          trigger: "blur"
        },
        specsid: { required: true, message: "请选择规格", trigger: "blur" },
        specsattr: {
          required: true,
          message: "请选择规格属性值",
          trigger: "blur"
        },
        isnew: { required: true, message: "请选择", trigger: "blur" },
        ishot: { required: true, message: "请选择", trigger: "blur" },
        status: { required: true, message: "请选择", trigger: "blur" },
        description: { validator: validDesc, trigger: "blur" },
        img: { validator: validImg, trigger: "blur" }
      }
    }
  },
  created() {
    // 初始化分类列表
    if (this.cateList.length === 0) {
      this.getCateListActoin()
    }
    // 初始化规格列表
    if (this.specsList.length === 0) {
      this.getSpecsListAction({ page: 1, size: 100 })
    }
  },
  methods: {
    ...mapActions("category", ["getCateListActoin"]),
    ...mapActions("specs", ["getSpecsListAction"]),
    // 监听一级分类更新
    firstCateChange(id) {
      // 1-通过id查找索引
      const index = this.cateList.findIndex(item => item.id === id)
      this.secondCateList = this.cateList[index].children
    },
    // 监听规格的更新
    // true: 点击下拉框
    // false: 手动调用
    specsidChange(id, flag = true) {
      // 1-通过id查找索引
      const index = this.specsList.findIndex(item => item.id === id)
      // 更新规格属性可选值的数组
      this.specsAttrs = this.specsList[index].attrs
      // 点击属性规格下拉框的时候, 执行重置
      if (flag) {
        // 重置规格属性可选值
        this.goodsData.specsattr = []
      }
    },
    // 获取用户选择的图片对象
    imgChange(file) {
      // 更新数据
      this.goodsData.img = file.raw
    },
    // 上传图片数量超过限制的提示
    limitTips() {
      this.$error("最多只能上传一张图片")
    },
    // 表单提交
    submitForm() {
      this.$refs.goodsForm.validate(valid => {
        if (valid) {
          const fd = new FormData()
          // 对规格属性可选值进行类型转换 ['a','b','c']=>a,b,c
          this.goodsData.specsattr = this.goodsData.specsattr.join(",")
          // 获取富文本编辑器的内容
          this.goodsData.description = this.$refs.editor.getHtml()
          for (let key in this.goodsData) {
            fd.append(key, this.goodsData[key])
          }
          const url = this.goodsData.id > 0 ? "/goodsedit" : "/goodsadd"
          this.$http.post(url, fd).then(res => {
            if (res.code === 200) {
              this.$success(res.msg)
              // 重置信息
              return this.resetInfo()
            }
            this.$error(res.msg)
          })
        }
      })
    },
    // 重置信息
    resetInfo() {
      //  1-关闭模态框
      this.info.isShow = false
      //  2-重置表单信息
      this.goodsData = {
        id: 0,
        goodsname: "",
        first_cateid: "",
        second_cateid: "",
        specsattr: [],
        specsid: "",
        img: "",
        status: "",
        isnew: "",
        ishot: "",
        price: "",
        market_price: "",
        description: ""
      }
      // 图片上传组件
      this.imgList = []
      // 重置富文本编辑器的内容
      this.$refs.editor.setHtml("")
      // 3-更新列表视图: 通知父组件更新
      this.$emit("updateList")
    },
    // 设置表单信息
    setInfo(obj) {
      // 更新和表单进行绑定的数据对象
      delete obj.firstcatename
      delete obj.secondcatename
      this.goodsData = { ...obj }
      // 处理属性可选值的数据格式
      this.goodsData.specsattr = this.goodsData.specsattr.split(",")
      // 手动调用一级分类下拉框组件
      this.firstCateChange(obj.first_cateid)
      // 手动调用规格下拉框组件的change事件回调函数
      this.specsidChange(obj.specsid, false)
      // 设置商品图片
      this.imgList = obj.img !== "" ? [{ url: this.$imgBaseUrl + obj.img }] : []
      // 手动设置富文本编辑器的内容
      setTimeout(() => {
          this.$refs.editor.setHtml(obj.description)
      }, 200);
    }
  }
}
</script>

<style>
</style>


